import React, { Component } from 'react';
import {
  Card,
  Row,
  Col,
  message,
  Spin,
} from 'antd';
import Page from 'components/LayoutComponents/Page';
import { CartHeadAction } from 'components/common';
import { getQueryParams } from 'utils/urlUtil';
import {
  getPlanSubjectDetail,
  createUpdatePlanSubjectDetails,
  getTeacherInPlanSubjectDetail,
  exportPlanSubjectDetailExcel,
  exportPlanSubjectDetailPdf,
} from 'services/planSubjectDetail​';
import { makeid } from 'utils/functionUtil';
import { getFormTableData, convertTableDataObjToArray } from 'utils/formUtil';

import SearchBarTrainingPlan from './FormTrainingPlan';
import MenuTabTop from './MenuTabTop';
import TrainingPlanTableDetail from './TrainingPlanTableDetail';
import './style.scss';

class TrainingPlanTable extends Component {
  constructor(props) {
    super(props);
    this.refForm = React.createRef();
    this.state = {
      loading: false,
      reload: 0,
      dataTraningPlan: null,
      selectedRows: null,
      rowKey: "autoKey",
    };
  }

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  getTableData = (values) => {
    const { rowKey } = this.state;
    const formDataObj = getFormTableData(values, rowKey);
    const formDataArray = convertTableDataObjToArray(formDataObj);
    return formDataArray;
  }

  handleChangeQueryParams = async () => {
    const { loading } = this.state;
    if (loading) {
      return;
    }
    try {
      const paramSearch = getQueryParams();
      if (
        !!paramSearch.trainTypeId &&
        !!paramSearch.facultyId &&
        !!paramSearch.courseId &&
        !!paramSearch.studyFieldId &&
        !!paramSearch.yearEduId &&
        !!paramSearch.semesterId
      ) {
        this.setState({
          loading: true,
        });
        const response = await getPlanSubjectDetail(paramSearch);
        let tempData = [...response.data.items];
        tempData = tempData.map(item => {
          item.autoKey = `${makeid(8)}_${item.id}`;
          return item;
        })
        this.setState({
          dataTraningPlan: [...tempData],
        });
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  handleSelectedRows = (selectedRows) => {
    this.setState({
      selectedRows,
    });
  }

  handleSave = async () => {
    try {
      const { selectedRows, rowKey, dataTraningPlan } = this.state;
      if (selectedRows && selectedRows.length) {
        const listFieldsNeedValidate = [];
        const listSelectedKey = selectedRows.map(item => {
          return item[rowKey];
        });
        dataTraningPlan.forEach(item => {
          if(listSelectedKey.includes(item[rowKey])) {
            listFieldsNeedValidate.push(`studyPeriodTypeId-${item[rowKey]}`);
            listFieldsNeedValidate.push(`splitPeriod-${item[rowKey]}`);
            listFieldsNeedValidate.push(`studySessionId-${item[rowKey]}`);
            listFieldsNeedValidate.push(`teacherId-${item[rowKey]}`);
            listFieldsNeedValidate.push(`roomId-${item[rowKey]}`);
            listFieldsNeedValidate.push(`fromWeek-${item[rowKey]}`);
            listFieldsNeedValidate.push(`toWeek-${item[rowKey]}`);
          }
        });
        console.log('this.refForm.current: ', this.refForm.current);
        this.refForm.current.clearField();
        await this.refForm.current.validateFields(listFieldsNeedValidate, async (err, values) => {
          if (!err) {
            this.setState({
              loading: true,
            });
            const formDataArray = this.getTableData(values);
            const listSelectedItem = formDataArray.filter(item => listSelectedKey.includes(item[rowKey]));
            const listSelectedItemFullData = listSelectedItem.map(item => {
              const rawItem = dataTraningPlan.find(element => element.autoKey === item.autoKey);
              return Object.assign({}, rawItem, item);
            });
            const formatDataToSave = {
              planSubjectDetails: [...listSelectedItemFullData],
            }
            await createUpdatePlanSubjectDetails(formatDataToSave);
            this.setState({
              dataTraningPlan: null,
              selectedRows: null,
              loading: false,
            },
              () => this.handleChangeQueryParams(),
            );
            message.success('Lưu thành công');
          }
        });
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  handleAuto = async () => {
    try {
      this.refForm.current.validateFields(async (err, values) => {
        if (!err) {
          this.setState({
            loading: true,
            selectedRows: null,
          });
          const { dataTraningPlan } = this.state;
          const formDataArray = this.getTableData(values);
          const listSelectedItemFullData = formDataArray.map(item => {
            const rawItem = dataTraningPlan.find(element => element.autoKey === item.autoKey);
            return Object.assign({}, rawItem, item);
          });
          const formatDataToSave = {
            planSubjectDetails: [...listSelectedItemFullData],
          }
          const response = await getTeacherInPlanSubjectDetail(formatDataToSave);
          let tempData = [...response.data.items];
          tempData = tempData.map(item => {
            item.autoKey = `${makeid(8)}_${item.id}`;
            return item;
          })
          this.setState({
            dataTraningPlan: [...tempData],
          });
          message.success('Phân bổ tự động thành công');
        }
      });
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  handleMerge = () => {
    console.log('on merge');
  }

  handleExportPDF = async () => {
    try {
      this.setState({
        loading: true,
      });
      const paramSearch = getQueryParams();
      await exportPlanSubjectDetailPdf(paramSearch);

    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }

  handleExportExcel = async () => {
    try {
      this.setState({
        loading: true,
      });
      const paramSearch = getQueryParams();
      await exportPlanSubjectDetailExcel(paramSearch);
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }

  render() {
    const { dataTraningPlan, reload, rowKey, loading, selectedRows } = this.state;
    const isDisabledSave = !(selectedRows && selectedRows.length);
    const isDisabledAuto = !(dataTraningPlan && dataTraningPlan.length);
    return (
      <Spin spinning={loading}>
        <Card title="Phân bổ giáo viên, ca học, phòng học">
          <Row>
            <Col span={24}>
              <CartHeadAction>
                <MenuTabTop
                  onExportExcel={this.handleExportExcel}
                  onExportPDF={this.handleExportPDF}
                />
              </CartHeadAction>
            </Col>
            <Col span={24}>
              <SearchBarTrainingPlan
                onSave={this.handleSave}
                onAuto={this.handleAuto}
                onMerge={this.handleMerge}
                isDisabledSave={isDisabledSave}
                isDisabledAuto={isDisabledAuto}
              />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <TrainingPlanTableDetail
                reload={reload}
                data={dataTraningPlan}
                ref={this.refForm}
                handleSelectedRows={this.handleSelectedRows}
                rowKey={rowKey}
              />
            </Col>
          </Row>
        </Card>
      </Spin>
    );
  }
}

export default props => (
  <Page {...props}>
    <TrainingPlanTable />
  </Page>
);



